<template>
  <div>
    <headerComponent
      :title="title"
      :subTitle="subTitle"
      :isLoading="isLoading"
      :pageURL="pageURL"
    ></headerComponent>

    <div class="mdui-container" style="max-width: 600px">
      <br /><br />
      <div class="mdui-card mdui-hoverable" style="border-radius: 16px">
        <!-- 卡片头部，包含头像、标题、副标题 -->

        <!-- 卡片的媒体内容，可以包含图片、视频等媒体内容，以及标题、副标题 -->
        <div class="mdui-card-media">
          <img
            style="max-height: 1000px"
            src="https://static.llilii.cn/images/kagamine/78688114_p0.png"
          />
        </div>

        <!-- 卡片的标题和副标题 -->
        <div class="mdui-card-primary">
          <div class="mdui-card-primary-title">更多功能</div>
          <div class="mdui-card-primary-subtitle">一起来探索本站吧！</div>
        </div>

        <!-- 卡片的内容 -->
        <div class="mdui-card-content">
          本站更多功能正在开发中...<br />预计我们将会支持以下功能:<br />1.支持一键生成表白卡片<br />2.支持点赞表白<br />3.支持表白搜索<br />
        </div>
        <!--
        <div class="mdui-card-actions">
          <button class="mdui-btn mdui-btn-icon mdui-float-right">
            <i class="mdui-icon material-icons">share</i>
          </button>
          <button class="mdui-btn mdui-btn-icon mdui-float-right">
            <i class="mdui-icon material-icons">more</i>
          </button>
        </div>-->
      </div>
      <br /><br />
    </div>
  </div>
</template>
<script>
import headerComponent from "../components/header";
export default {
  data() {
    return {
      title: "未知表白墙",
      subTitle: "更多功能",
      isLoading: false,
      pageURL:"/more",
    };
  },
  components: {
    headerComponent: headerComponent,
  },
};
</script>



